import axios from "@/util/fetch.js";
export default {
  name: "cbox",
  data() {
    return {
      id: "",
      applydata: "",
      detailurl: "/review/getdetail"
    };
  },
  mounted() {
    this.id = this.$route.params.id;
    axios
      .post(this.detailurl, {
        id: this.id
      })
      .then(res => {
        console.log(res);
        this.applydata = res.data;
      });
  },
  methods: {
    acstatus(item) {
      if (item.state == 5) {
        return "发起审批";
      } else if (item.state == 4) {
        return "已撤销";
      } else if (item.state == 3) {
        return "待审批";
      } else if (item.state == 2) {
        return "已同意";
      } else if (item.state == 1) {
        return "被驳回";
      } else if (item.state == 0) {
        return "审批中";
      }
    },
    applydatas() {
      this.$refs.applydatas.style.display = "none";
      window.print();
    }
  }
};



