import axios from "@/util/fetch.js";
import Header from "../nav/header.vue";
export default {
    components:{
        Header
    },
    data() {
        return {
            mlist:[
                {name:"人事行政",count:6,show:true},
                {name:"其他",count:1,show:true},
            ],
            rlist:[
                {image:require('../../assets/icon/op1.png'),word:"请假",url:"/leave"},
                {image:require('../../assets/icon/op2.png'),word:"出差",url:"/beaway"},
                {image:require('../../assets/icon/op3.png'),word:"外出",url:"/beout"},
                {image:require('../../assets/icon/op4.png'),word:"加班",url:"/overtime"},
                {image:require('../../assets/icon/op5.png'),word:"人员招聘",url:"/employ"},
                {image:require('../../assets/icon/op6.png'),word:"印章申请",url:"/stamper"},
                {image:require('../../assets/icon/op7.png'),word:"通用审批",url:"/comapprova"},
            ],
            up:"ios-arrow-up",
            down:"ios-arrow-down",
            url:'',
            Login_token:''
        }
    },

    methods:{
       clickdata(index){
           this.mlist[index].show = !this.mlist[index].show;
       },
    },
    mounted: function() {
        this.Login_token=sessionStorage.getItem("Login_token"); 
        this.$refs.apy_loding.style.display = "block";
        axios.post("/review").then(res=>{
            if(res.status==200){
                console.log(res);
                this.$refs.apy_loding.style.display = "none";
            }
        })
  },
}



