import axios from "@/util/fetch.js";
import Header from "../nav/header.vue";
import Newadd from "../nav/newadd.vue";
export default {
  components:{
     Header,
     Newadd
  },   
  //通过两个钩子函数来添加移除当前页面的颜色
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "add_bg";
  },
  beforeDestroy: function() {
    document.body.removeAttribute("class", "add_bg");
  },
  name: "list",
  data() {
    return {
      values: "",
      active: "",
      typeData: [{ name: "全部",type:''}, { name: "请假",type:1 }, { name: "出差",type:2 },{ name: "加班",type:3 },{ name: "通用审批",type:4 },{ name: "人员需求",type:13 },{ name: "印章领用",type:14 },{ name: "外出申请",type:15 },{ name: "借款申请",type:16}],
      stateData: [{ name: "全部",status:''}, { name: "审批完成",status:2 }, { name: "被驳回",status:1 }],
      reviewlist: [],
      cuid:'',//查看人的UID
      ltype:1,//1待我审批 2已审批的
      keyword:'',//搜索关键字
      type:'',//审批类型
      status:'',//审批状态
      limit:10,
      condition:{}
    };
  },
  

  mounted: function() {
      //保存条件
        this.condition={};
        this.condition.ltype=1;
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/checkbyme",this.condition,1)
  },

  methods: {
    // 点击顶部导航待我审批和我已审批按钮加载数据
        getlist(ltype) {
        this.$refs.filt.style.display = "none";
        this.$refs.filts.style.display = "none";
        this.$refs.seek.style.display = "none";
        this.$refs.seeks.style.display = "none";
        this.ltype=ltype;
        this.reviewlist = [];
        //保存条件
        this.condition={};
        this.condition.ltype=ltype;
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/checkbyme",this.condition,1)
    },

    // 选择类型并加载数据
    showtype(item,index) {
        this.$refs.loadmore.style.display = "none";
        this.active = index;
        this.reviewlist = [];
        this.condition={};
        this.condition.ltype=this.ltype;
        this.condition.type=item.type;
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/checkbyme",this.condition,1)
    },
    
    // 选择状态并加载数据
    showstate(item,index) {
        this.$refs.loadmore.style.display = "none";
        this.active = index;
        this.reviewlist = [];
        this.condition={};
        this.condition.ltype=this.ltype;
        this.condition.status=item.status;
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/checkbyme",this.condition,1)
    },

    // 打开待我审批的搜索框
    showcondition1() {
        this.page=1;
        this.limit=5;
        this.keyword='';
        this.reviewlist = [];
        if (this.$refs.seek.style.display == "block") {
            this.$refs.seek.style.display = "none";
            this.$refs.filt.style.display = "none";
            this.keyword='';
        } else {
            this.$refs.seek.style.display = "block";
            this.$refs.filt.style.display = "none";
        }
        this.$refs.loadmore.style.display = "none";
    },
    
    // 打开待我审批的筛选框
    showcondition2() {
        this.active='';
        this.reviewlist = [];
        if (this.$refs.filt.style.display == "block") {
            this.$refs.seek.style.display = "none";
            this.$refs.filt.style.display = "none";
        } else {
            this.$refs.seek.style.display = "none";
            this.$refs.filt.style.display = "block";
        }
        this.condition={};
        this.condition.ltype=this.ltype;
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/checkbyme",this.condition,1)
    },

    // 打开我已审批的搜索框
    showcondition3() {
        this.page=1;
        this.limit=5;
        this.keyword='';
        this.reviewlist = [];
        if (this.$refs.seeks.style.display == "block") {
            this.keyword='';
            this.$refs.seeks.style.display = "none";
            this.$refs.filts.style.display = "none";
        } else {
            this.$refs.seeks.style.display = "block";
            this.$refs.filts.style.display = "none";
        }
        this.$refs.loadmore.style.display = "none";
    },

    // 打开我已审批的筛选条件
    showcondition4() {
        this.active='';
        this.reviewlist = [];
        //切换筛选框显示
        if (this.$refs.filts.style.display == "block") {
            this.$refs.filts.style.display = "none";
            this.$refs.seeks.style.display = "none";
        } else {
            this.$refs.filts.style.display = "block";
            this.$refs.seeks.style.display = "none";
            this.condition={};
            this.condition.ltype=this.ltype;
            this.condition.page=1;
            this.condition.limit=10;
            this.getData("/review/checkbyme",this.condition,1)
        }
        // this.$refs.loading.style.display = "block";
        //打开蒙层，并默认加载审批完成数据
    },
    
    // 按关键字搜索
    searchreview() {
        if(this.keyword==''){
            this.$Message.error("请输入关键字");
            return false;
        }
        this.reviewlist = [];
        this.condition={};
        this.condition.ltype=this.ltype;
        this.condition.page=1;
        this.condition.limit=10;
        this.condition.keyword=this.keyword;
        this.getData("/review/checkbyme",this.condition,1)
    },

    //加载更多
    loadmore() {
        this.$refs.loadmore.style.display = "none";
        this.condition.page += 1;
        this.getData("/review/checkbyme",this.condition,0)
    },

    //封装axios数据请求回调处理方法
    getData(url,data,loadtype){
        console.log(data);
        //判断数据加载方式打开对应的蒙层
        if(loadtype==0){
            this.$refs.dataloading.style.display = "block";
        }else{
            this.$refs.loading.style.display = "block"; 
        }
        //请求处理数据
        axios
            .post(url,data)
            .then(res => {  
                for (let i in res.data.list) {
                    this.reviewlist.push({
                        id: res.data.list[i].id,
                        apply_man: res.data.list[i].apply_man,
                        uid:res.data.list[i].uid,
                        sn:res.data.list[i].sn,
                        type:res.data.list[i].type,
                        lid:res.data.list[i].lid,
                        lname:res.data.list[i].lname,
                        start:res.data.list[i].start,
                        end:res.data.list[i].end,
                        period:res.data.list[i].period,
                        reason:res.data.list[i].reason,
                        status:res.data.list[i].status,
                        title:res.data.list[i].title,
                        content:res.data.list[i].content,
                        money:res.data.list[i].money,
                        address:res.data.list[i].address,
                        duty:res.data.list[i].duty,
                        number:res.data.list[i].number,
                        salary:res.data.list[i].salary,
                        intime:res.data.list[i].intime,
                        addtime:res.data.list[i].addtime,
                        endtime:res.data.list[i].endtime,
                        acid:res.data.list[i].acid
                    })
                }
                //符合条件的数据总条数大于列表中的条数和大于每页条数，则提示加载更多
                if(res.data.count>this.limit && this.reviewlist.length<res.data.count){
                    this.$refs.loadmore.style.display = "block";
                }
                //判断数据加载方式关闭对应的蒙层
                if(loadtype==0){
                    this.$refs.dataloading.style.display = "none";
                }else{
                    this.$refs.loading.style.display = "none";
                }
                //存储查看人UID
                this.cuid=res.data.cuid;   
        });
    },

    // 审核状态
    getstatus(item) {
        if (item.status == 0) {
            return "审核中";
        } else if (item.status == 1) {
            return "被驳回";
        } else if (item.status == 2) {
            return "已完成";
        }else if (item.status == 3) {
            return "已撤销";
        }else{
            return "已被行政撤销";
        }
    },

    //名字截取
    names(item) {
        if (item.apply_man.length == 2) {
            return item.apply_man;
        } else if (item.apply_man.length == 3) {
            return item.apply_man.substring(1, 3);
        } else if (item.apply_man.length == 4) {
            return item.apply_man.substring(2, 4);
        }
    },

    jump(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.id,
          acid:item.acid,
          optype:1,
          block:sessionStorage.getItem("block")
        }
      })
    }

  }
}



