
import axios from "@/util/fetch.js";
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Header from "../nav/header.vue";
import Newadd from "../nav/newadd.vue";
export default {
directives: {
TransferDom
},
components: {
Header,
Newadd,
Previewer
},


beforeCreate: function() {
document.getElementsByTagName("body")[0].className = "add_bg";
},
beforeDestroy: function() {
document.body.removeAttribute("class", "add_bg");
},
name: "detail",
inject: ["reload"],
data() {
return {
applydata: {},
id: "", //审批ID
lid: "", //审批类型
type: "", //审批大类
acid: "", //审核表ID
optype: "", //查看人操作状态
cuid: "", //当前查看人ID
apply_man: "",
images: [],
files: [],
areanames: [],
addname: "",
search: "", //搜索
search_data: [], //搜索
checkcomment: [],
copymans: [],
modal1: false,
modal2: false,
modal_loading: false,
editurl: "",
comimg: "",
list: [],
options: {
getThumbBoundsFn(index) {
let thumbnail = document.querySelectorAll(".img-list")[index];
let pageYScroll =
window.pageYOffset || document.documentElement.scrollTop;
let rect = thumbnail.getBoundingClientRect();
return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };
}
}
};
},
mounted: function() {

this.$refs.loading.style.display = "block";
this.id = this.$route.params.id;
this.acid = this.$route.params.acid;
this.optype = this.$route.params.optype;


if (
/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i.test(
navigator.userAgent
)
) {
this.$refs.print.style.display = "none";
} else {
this.$refs.print.style.display = "block";
}

axios
.post("/review/getmessage", {
id: this.id,
acid: this.acid,
optype: this.optype
})
.then(res => {
console.log(res);
this.$refs.loading.style.display = "none";
if (res.data.Code == 202) {
if (this.optype == 1) {
this.$router.push({
name: "checkbyme"
});
} else if (this.optype == 2) {
this.$router.push({
name: "sendbyme"
});
} else if (this.optype == 3) {
this.$router.push({
name: "copybyme"
});
}
} else {
this.applydata = res.data;
this.lid = res.data.lid;
this.type = res.data.type;
this.apply_man = res.data.apply_man;
this.checkcomment = res.data.checkcomment;
this.copymans = res.data.copymans;
console.log(this.copymans);
for (let i in res.data.files) {
if (res.data.files[i].ftype == 1) {
this.list.push({
src:
"http://p.yangguangzhiye.cn/review/upload" +
res.data.files[i].path,
msrc:
"http://p.yangguangzhiye.cn/review/upload" +
res.data.files[i].path
});
} else {
this.files.push({
fname: res.data.files[i].fname,
ftype: res.data.files[i].ftype,
path: res.data.files[i].path,
timein: res.data.files[i].timein
});
}
}
}
});
},


methods: {
logIndexChange(arg) {
console.log(arg);
},
show(index) {
this.$refs.previewer.show(index);
},
jumpPass() {
this.optype = 1;
this.$router.push({
name: "review",
params: {
rid: this.id,
acid: this.acid,
status: 2,
optype: this.optype
}
});
},


jumpReject() {
this.optype = 1;
this.$router.push({
name: "review",
params: {
rid: this.id,
acid: this.acid,
status: 1,
optype: this.optype
}
});
},


transmit() {
this.optype = 1;
this.$router.push({
name: "transmit",
params: {
rid: this.id,
acid: this.acid,
optype: this.optype
}
});
},


jumpComment() {
this.$router.push({
name: "comment",
params: {
rid: this.id,
acid: this.acid,
optype: this.optype
}
});
},


jumpCancel() {
this.modal1 = false;
this.$refs.loadings.style.display = "block";
axios
.post("/review/revoke", {
id: this.id,
acid: this.acid,
optype: this.optype
})
.then(res => {
if (res.data.Code == "200") {
this.$Message.success(res.data.Message);
this.$refs.loadings.style.display = "none";
this.reload();
} else if (res.data.Code == "202") {
this.$Message.error(res.data.Message);
this.$refs.loadings.style.display = "none";
}
});
},


jumpDelete() {
this.modal2 = false;
this.$refs.loadings.style.display = "block";
axios
.post("/review/delete", {
id: this.id,
acid: this.acid,
optype: this.optype
})
.then(res => {
if (res.data.Code == "200") {
this.$Message.success(res.data.Message);
this.$router.push({
name: "sendbyme"
});
this.$refs.loadings.style.display = "none";
} else if (res.data.Code == "202") {
this.$Message.error(res.data.Message);
this.$refs.loadings.style.display = "none";
}
});
},


jumpEdit() {
this.editurl = "";
if (this.type == 1) {
this.editurl = "leave_edit";
} else if (this.type == 2) {
this.editurl = "beaway_edit";
} else if (this.type == 3) {
this.editurl = "overtime_edit";
} else if (this.type == 4) {
this.editurl = "comapprova_edit";
} else if (this.type == 13) {
this.editurl = "employ_edit";
} else if (this.type == 14) {
this.editurl = "stamper_edit";
} else if (this.type == 15) {
this.editurl = "beout_edit";
}

this.$router.push({
name: this.editurl,
params: {
id: this.id
}
});
},

copy_add() {
this.plus_i = "cope";
this.$refs.sltmc.style.display = "block";
axios.post("/review/area").then(res => {
this.areanames = res.data;
});
},


sltmc_details(item) {
this.$refs.loading.style.display = "block";
axios
.post("/review/area", {
id: item.id,
type: this.ids
})
.then(res => {
this.areanames = res.data;
this.$refs.loading.style.display = "none";
});
},

sltmc_hide() {
this.$refs.sltmc.style.display = "none";
},
sltmc_show() {
this.$refs.search.style.display = "none";
this.$refs.sltmc_bottom.style.display = "block";
if (this.biaoshi == "pid") {
this.$refs.loading.style.display = "block";
axios
.post("/review/area", {
id: this.id2,
type: "cid"
})
.then(res => {
this.$refs.loading.style.display = "none";
this.areanames = res.data;
});
this.biaoshi = "did";
} else if (this.biaoshi == "did") {
this.$refs.loading.style.display = "block";
axios
.post("/review/area", {
id: this.id1,
type: "aid"
})
.then(res => {
this.$refs.loading.style.display = "none";
this.areanames = res.data;
});
this.biaoshi = "cid";
} else if (this.biaoshi == "cid") {
this.$refs.loading.style.display = "block";
axios.post("/review/area").then(res => {
this.$refs.loading.style.display = "none";
this.areanames = res.data;
});
this.biaoshi = "aid";
}
},


getid(item) {
if (item.type == "aid") {
this.id1 = item.id;
this.biaoshi = "cid";
return (this.ids = "aid");
} else if (item.type == "cid") {
this.id2 = item.id;
this.biaoshi = "did";
return (this.ids = "cid");
} else if (item.type == "did") {
this.biaoshi = "pid";
return (this.ids = "did");
} else if (item.type == "pid") {
if (this.plus_i == undefined) {
this.check_man.push({
check_man_name: item.name,
check_man_uid: item.id
});
location.href = "javascript:scrollTo(0,1000)";
} else if (this.plus_i == "cope") {

axios
.post("/review/addcopyman", {
rid: this.id,
uid: item.id
})
.then(res => {
if (res.data.Code == "200") {
this.copymans.push({
realname: item.name,
uid: item.id
});
this.$Message.success(res.data.Message);
} else if (res.data.Code == "202") {
this.$Message.error(res.data.Message);
}
});

console.log(this.copymans);
location.href = "javascript:scrollTo(0,1000)";
this.plus_i = undefined;
} else {
this.check_man.splice(this.plus_i, 0, {
check_man_name: item.name,
check_man_uid: item.id
});
location.href = "javascript:scrollTo(0,1000)";
this.plus_i = undefined;
}
this.$refs.sltmc.style.display = "none";
}
},


search_man() {
this.$refs.loading.style.display = "block";
axios
.post("/review/searchman", {
name: this.search
})
.then(res => {
this.$refs.loading.style.display = "none";
if (res.data.length == 0) {
this.$Message.config({
top: 300,
duration: 1
});
this.$Message.warning("公司无此人");
} else {
this.search_data = res.data;
}
});
if ((this.$refs.sltmc_bottom.style.display = "block")) {
this.$refs.sltmc_bottom.style.display = "none";
this.$refs.search.style.display = "block";
} else {
this.$refs.sltmc_bottom.style.display = "block";
this.$refs.search.style.display = "none";
}
},

sahName(n) {
if (n.name.length == 2) {
return n.name;
} else if (n.name.length == 3) {
return n.name.substring(1, 3);
} else if (n.name.length == 4) {
return n.name.substring(2, 4);
}
},

cname(n) {
if (n.cname.length > 7) {
return n.cname.substring(0, 5) + "...";
} else {
return n.cname;
}
},

search_slt(n) {
this.search = "";
this.$refs.search.style.display = "none";
this.$refs.sltmc_bottom.style.display = "block";
if (n.type == "pid") {
if (this.plus_i == undefined) {
this.check_man.push({
check_man_name: n.name,
check_man_uid: n.id
});
} else if (this.plus_i == "cope") {

axios
.post("/review/addcopyman", {
rid: this.id,
uid: n.id
})
.then(res => {
if (res.data.Code == "200") {
this.copymans.push({
realname: n.name,
uid: n.id
});
this.$Message.success(res.data.Message);
} else if (res.data.Code == "202") {
this.$Message.error(res.data.Message);
}
});

console.log(this.copymans);
this.plus_i = undefined;
} else {
this.check_man.splice(this.plus_i, 0, {
check_man_name: n.name,
check_man_uid: n.id
});
this.plus_i = undefined;
}
this.$refs.sltmc.style.display = "none";
}
},

photo_hide() {
this.$refs.comimg.style.display = "none";
},

com_img_show(files, n) {
this.comimg = files.filepath;
this.$refs.comimg.style.display = "block";
},


preview(index) {
this.$imagePreview({
images: this.images,
index: index
});
},


pass(color) {
this.$refs.pass.style.background = color;
},
jet(color) {
this.$refs.jet.style.background = color;
},
care(color) {
this.$refs.care.style.background = color;
},
cmt(color) {
this.$refs.cmt.style.background = color;
},
rcn(color) {
this.$refs.rcn.style.background = color;
},
del(color) {
this.$refs.del.style.background = color;
},
mdn(color) {
this.$refs.mdn.style.background = color;
},


ckstate(item) {
if (item.state == 5) {
return "发起审批";
} else if (item.state == 4) {
return "已撤销";
} else if (item.state == 3) {
return "待审批";
} else if (item.state == 1) {
return "已驳回";
} else if (item.state == 2) {
return "通过审批";
} else if (item.state == 0) {
if (this.applydata.nowstatus == 0) {
return "Hi，轮到你审批了，赶快处理吧！";
} else {
if (item.isread == 0) {
return "待审批(未读)";
} else {
return "审批中(已读 " + item.ctime + ")";
}
}
} else if (item.state == 7) {
return "转交给" + item.receiver;
} else if (item.state == 6) {
return "同意并转交给" + item.receiver;
} else if (item.state == 8) {
return "评论";
}
},


realname(item) {
if(item.realname){
if (item.realname.length == 2) {
return item.realname;
} else if (item.realname.length == 3) {
return item.realname.substring(1, 3);
} else if (item.realname.length == 4) {
return item.realname.substring(2, 4);
}
}
},


message(res) {
if (res.data.Code == "200") {
this.$Message.config({
top: 300,
duration: 1
});
this.$Message.success(res.data.Message);
} else if (res.data.Code == "202") {
this.$Message.config({
top: 300,
duration: 1
});
this.$Message.error(res.data.Message);
}
},

reDelete() {
this.$Message.config({
top: 300,
duration: 1
});
this.$Message.warning("删除失败，请重试");
},

resultData() {
if (this.result == "0") {
return "审批通过";
} else if (this.result == "1") {
return "等我审批";
} else if (this.result == "2") {
return "审批拒绝";
}
},


name() {
if (this.apply_man.length == 2) {
return this.apply_man;
} else if (this.apply_man.length == 3) {
return this.apply_man.substring(1, 3);
} else if (this.apply_man.length == 4) {
return this.apply_man.substring(2, 4);
}
},


status() {
if (this.applydata.status == 0) {
return "正在审核中";
} else if (this.applydata.status == 1) {
return "审批被驳回";
} else if (this.applydata.status == 2) {
return "审批已通过";
} else if (this.applydata.status == 3) {
return "审批已撤销";
}
},


pdetail(id) {
this.$router.push({
name: "pdetail",
params: {
id: id
}
});
}
}
};



