window.onblur = function() {
  var inputs = document.getElementsByTagName("input");
  for (var i = 0; i < inputs.length; i++) {
    document.getElementsByTagName("input")[i].blur();
  }
};
import axios from "axios";
document.title = "行政审批";
export default {
   provide() {
    return {
        reload: this.reload
    }
  },
  data() {
    return {
      isRouterAlive:true,
        url: encodeURIComponent("http://voa.yangguangzhiye.cn/review/#"),
    //   url: encodeURIComponent("http://127.0.0.1:8080/#"),
      dateUrl: null,
      http_url: "http://woa.yangguangzhiye.cn/getLogin_qy.html",
    //   http_url: "http://www.zwoa.com/getLogin_qy.html",

      Code: "",
      systemid: "",
      api_token: "",
      Login_token: "",
      Message: "",
      datetime: "",
      Login_datetime: "",
      block:'',
      appmc:false,
    };
  },
  beforeMount: function() {
    // sessionStorage.setItem("Login_token",'53d36301b0aa768f39c931b4c2fe86f85b634065');
    // sessionStorage.setItem("block",1);
    //获取企业微信中菜单栏链接上或者消息入口链接的block
      if(this.$route.query.block){
        this.block=this.$route.query.block;
      }else if(this.$route.params.block){
        this.block=this.$route.params.block;
      };
   
      // 存储获取的block和设定API_TOKEN
      if(this.block==1){
        this.systemid=121;
        this.api_token="45dcd0929a1453f9715ae973d7428708b5f34c8e";
        sessionStorage.setItem("block", 1);
      }else if(this.block==2){
        this.systemid=120;
        this.api_token="8eaa9e0279be54f7946f0ce3c2a21e7fe7e14256";
        sessionStorage.setItem("block", 2);
      };
      //如果回调域名中传来的code=202，提示消息
      if(this.$route.query.Code == 202){
          this.Message=this.$route.query.Message;
          this.appmc=true;
      };

      //判断是否存储Login_token 如果有存储超时10800要重新请求
      if(sessionStorage.getItem("Login_token")){
        this.Login_datetime=sessionStorage.getItem("Login_datetime");
        let newdate=Date.parse(new Date());
        let datetimes=(Number(newdate)-Number(this.Login_datetime))/1000;
        if(datetimes>10800){
          sessionStorage.clear();
          this.dateUrl = new Date();
          this.dateUrl = Date.parse(this.dateUrl) / 1000;
          location.href = `${this.http_url}?systemid=${this.systemid}&api_token=${
            this.api_token
          }&timestamp=${this.dateUrl}&redirect_uri=${this.url}${
            this.$route.path
          }`;
        }
      }else{
        //首次请求Login_token通过code返回值判断，成功则保存至浏览器端，未请求或者失败则再次发起请求  
        if(this.$route.query.Code){
          //code=200 Login_token保存至浏览器端
          if(this.$route.query.Code == 200){
            this.datetime = Date.parse(new Date());
            sessionStorage.setItem("Login_token", this.$route.query.Login_token);
            sessionStorage.setItem("Login_datetime", this.datetime);
              if(this.$route.query.block){
                this.block=this.$route.query.block;
              }else if(this.$route.params.block){
                this.block=this.$route.params.block;
              };
              if(this.block==1){
                this.systemid=121;
                this.api_token="45dcd0929a1453f9715ae973d7428708b5f34c8e";
                sessionStorage.setItem("block", 1);
              }else if(this.block==2){
                this.systemid=120;
                this.api_token="8eaa9e0279be54f7946f0ce3c2a21e7fe7e14256";
                sessionStorage.setItem("block", 2);
              };
          }
        }else{
          //code!=200 Login_token发起请求
          this.dateUrl = new Date();
          this.dateUrl = Date.parse(this.dateUrl) / 1000;
          location.href = `${this.http_url}?systemid=${this.systemid}&api_token=${
            this.api_token
          }&timestamp=${this.dateUrl}&redirect_uri=${this.url}${
            this.$route.path
          }`;
        }
      }
  },
  methods: {
    reload() {
        this.isRouterAlive = false;
        this.$nextTick(function(){
            this.isRouterAlive = true;
        })
    },
    appMc_hide() {
      this.appmc=false;
    }
 }
};



