// import axios from 'axios';
import axios from "@/util/fetch.js";
import Header from "../nav/header.vue";
export default {
  components: {
    Header
  },
  inject: ["reload"],
  data() {
    return {
      formValidate: {
        type: 2,
        lid: 2,
        address: "",
        start: "请选择时间",
        end: "请选择时间",
        period: "",
        reason: ""
      },
      ruleValidate: {
        address: [
          {
            required: true,
            type: "string",
            message: "出差地点不能为空",
            trigger: "blur"
          }
        ],
        period: [
          {
            required: true,
            type: "number",
            message: "请假时数必须为数字且不能为空",
            trigger: "blur"
          }
        ],
        reason: [
          {
            required: true,
            type: "string",
            message: "出差原因不能为空",
            trigger: "blur"
          }
        ]
      },
      Login_token: "",
      visible: false,
      uploadList: [],
      defaultList: [],
      check_man: [],
      copy_man: [],
      plus_i: undefined,
      cope_i: undefined,
      areanames: [],
      addname: "",
      ids: "",
      ides: "",
      search: "", //搜索
      search_data: [], //搜索
      id1: "",
      id2: "",
      proposer: "",
      showUrl: "http://p.yangguangzhiye.cn/review/upload",
      showImg: "",
      ptoShow: [],
      txtHide: [],
      ptoShows: [],
      txtHides: [],
      block: ""
    };
  },
  mounted: function() {
    this.Login_token = sessionStorage.getItem("Login_token");
    this.block = sessionStorage.getItem("block");
    this.$refs.apy_loding.style.display = "block";
    this.uploadList = this.$refs.upload.fileList;
    axios.post("/review/add").then(res => {
      console.log(res);
      this.proposer = res.data.apply_man.realname;
      if (res.data.check_man) {
        this.check_man = res.data.check_man;
      }
      if (res.data.copy_man.copy_man_name) {
        this.copy_man = res.data.copy_man;
      }
      this.$refs.apy_loding.style.display = "none";
    });
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        //保单验证通过后判断审批人是否添加，抄送人可不加
        if (valid) {
          if (this.formValidate.start == "请选择时间") {
            this.$Message.error("开始时间不能为空!");
            return false;
          }
          if (this.formValidate.end == "请选择时间") {
            this.$Message.error("结束时间不能为空!");
            return false;
          }
          if (this.formValidate.start > this.formValidate.end) {
            this.$Message.error("开始时间不能大于结束时间!");
            return false;
          }
          if (this.check_man.length > 0) {
            //组装图片文件数组
            this.ptoShows = [];
            for (let i in this.ptoShow) {
              this.ptoShows.push({
                response: this.ptoShow[i].response
              });
            }
            this.txtHides = [];
            for (let i in this.txtHide) {
              this.txtHides.push({
                response: this.txtHide[i].response
              });
            }
            this.$refs.apy_loding.style.display = "block";
            axios
              .post("/review/save", {
                formdata: this.formValidate,
                imgs: this.ptoShows,
                files: this.txtHides,
                check_man: this.check_man,
                copy_man: this.copy_man
              })
              .then(res => {
                if (res.data.Code == "200") {
                  this.$Message.success(res.data.Message);
                  // this.reload();
                  this.$router.push({
                    name: "sendbyme"
                  });
                  this.$refs.apy_loding.style.display = "none";
                } else if (res.data.Code == "202") {
                  this.$Message.error(res.data.Message);
                  this.$refs.apy_loding.style.display = "none";
                }
              });
          } else {
            this.$Message.config({
              top: 300,
              duration: 1
            });
            this.$Message.error("请选择至少一个审批人");
          }
        } else {
          this.$Message.error("请检查表单是否填写完整!");
        }
      });
    },
    handleView(name) {
      this.imgName = name;
      this.visible = true;
    },
    //删除图片
    handleRemove(file) {
      const fileList = this.$refs.upload.fileList;
      this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
      axios
        .post("/review/delephoto", { path: file.name })
        .then(res => {
          console.log("成功", res, "图片", fileList);
        })
        .catch(req => {
          console.log("失败", req);
        });
    },
    //图片上传成功后将数据存储到ptoshow，并拼接返回数据构成图片url
    handleSuccess(res, file, fileList) {
      this.showImg = file.response.url;
      this.ptoShow = fileList;
      file.url = `${this.showUrl}${this.showImg}`;
      file.name = this.showImg;
    },

    handleFormatError(file) {
      this.$Notice.warning({
        title: "上传失败",
        desc: "文件格式错误，" + " 图片请上传png或者png"
      });
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "图片过大",
        desc: "上传图片不能超过2M"
      });
    },
    handleBeforeUpload() {
      const check = this.uploadList.length < 100;
      if (!check) {
        this.$Notice.warning({
          title: "一次上传图片不能超过100张"
        });
      }
      return check;
    },
    handleFj(file, response, fileList) {
      this.txtHide = fileList;
    },
    handleFmove(file, fileList) {
      axios
        .post("/review/delephoto", { path: file.response.url })
        .then(res => {});
    },
    names() {
      if (this.proposer.length == 2) {
        return this.proposer;
      } else if (this.proposer.length == 3) {
        return this.proposer.substring(1, 3);
      } else if (this.proposer.length == 4) {
        return this.proposer.substring(2, 4);
      }
    },
    check_names(item) {
      if (item.check_man_name.length == 2) {
        return item.check_man_name;
      } else if (item.check_man_name.length == 3) {
        return item.check_man_name.substring(1, 3);
      } else if (item.check_man_name.length == 4) {
        return item.check_man_name.substring(2, 4);
      }
    },
    copy_names(item) {
      if (item.copy_man_name.length == 2) {
        return item.copy_man_name;
      } else if (item.copy_man_name.length == 3) {
        return item.copy_man_name.substring(1, 3);
      } else if (item.copy_man_name.length == 4) {
        return item.copy_man_name.substring(2, 4);
      }
    },
    // 删除审批人
    check_hide: function(i) {
      this.check_man.splice(i, 1);
    },
    // 添加审批人
    check_add: function() {
      this.$refs.sltmc.style.display = "block";
      axios.post("/review/area").then(res => {
        this.areanames = res.data;
      });
    },
    apply_plus(i, item) {
      this.plus_i = i;
      this.$refs.sltmc.style.display = "block";
      axios.post("/review/area").then(res => {
        this.areanames = res.data;
      });
    },
    copy_add() {
      this.plus_i = "cope";
      this.$refs.sltmc.style.display = "block";
      axios.post("/review/area").then(res => {
        this.areanames = res.data;
      });
    },
    copy_hide(i) {
      this.copy_man.splice(i, 1);
    },
    // 选择审批人
    id(item) {
      if (item.type == "aid") {
        this.id1 = item.id;
        this.biaoshi = "cid";
        return (this.ids = "aid");
      } else if (item.type == "cid") {
        this.id2 = item.id;
        this.biaoshi = "did";
        return (this.ids = "cid");
      } else if (item.type == "did") {
        this.biaoshi = "pid";
        return (this.ids = "did");
      } else if (item.type == "pid") {
        if (this.plus_i == undefined) {
          this.check_man.push({
            check_man_name: item.name,
            check_man_uid: item.id
          });
          location.href = "javascript:scrollTo(0,1000)";
        } else if (this.plus_i == "cope") {
          this.copy_man.push({
            copy_man_name: item.name,
            copy_man_uid: item.id
          });
          location.href = "javascript:scrollTo(0,1000)";
          this.plus_i = undefined;
        } else {
          this.check_man.splice(this.plus_i, 0, {
            check_man_name: item.name,
            check_man_uid: item.id
          });
          location.href = "javascript:scrollTo(0,1000)";
          this.plus_i = undefined;
        }
        this.$refs.sltmc.style.display = "none";
      }
    },
    // 审核人
    sltmc_details(item) {
      this.$refs.apy_loding.style.display = "block";
      axios
        .post("/review/area", {
          id: item.id,
          type: this.ids
        })
        .then(res => {
          this.areanames = res.data;
          this.$refs.apy_loding.style.display = "none";
        });
    },
    // 搜索
    search_man() {
      this.$refs.apy_loding.style.display = "block";
      axios.post("/review/searchman", { name: this.search }).then(res => {
        this.$refs.apy_loding.style.display = "none";
        if (res.data.length == 0) {
          this.$Message.config({
            top: 300,
            duration: 1
          });
          this.$Message.warning("公司无此人");
        } else {
          this.search_data = res.data;
        }
      });
      if ((this.$refs.sltmc_bottom.style.display = "block")) {
        this.$refs.sltmc_bottom.style.display = "none";
        this.$refs.search.style.display = "block";
      } else {
        this.$refs.sltmc_bottom.style.display = "block";
        this.$refs.search.style.display = "none";
      }
    },
    sahName(n) {
      if (n.name.length == 2) {
        return n.name;
      } else if (n.name.length == 3) {
        return n.name.substring(1, 3);
      } else if (n.name.length == 4) {
        return n.name.substring(2, 4);
      }
    },
    cname(n) {
      if (n.cname.length > 7) {
        return n.cname.substring(0, 5) + "...";
      } else {
        return n.cname;
      }
    },
    search_slt(n) {
      this.search = "";
      this.$refs.search.style.display = "none";
      this.$refs.sltmc_bottom.style.display = "block";
      if (n.type == "pid") {
        if (this.plus_i == undefined) {
          this.check_man.push({
            check_man_name: n.name,
            check_man_uid: n.id
          });
        } else if (this.plus_i == "cope") {
          this.copy_man.push({
            copy_man_name: n.name,
            copy_man_uid: n.id
          });
          this.plus_i = undefined;
        } else {
          this.check_man.splice(this.plus_i, 0, {
            check_man_name: n.name,
            check_man_uid: n.id
          });
          this.plus_i = undefined;
        }
        this.$refs.sltmc.style.display = "none";
      }
    },
    sltmc_hide() {
      this.$refs.sltmc.style.display = "none";
    },
    sltmc_show() {
      this.$refs.search.style.display = "none";
      this.$refs.sltmc_bottom.style.display = "block";
      if (this.biaoshi == "pid") {
        this.$refs.apy_loding.style.display = "block";
        axios
          .post("/review/area", {
            id: this.id2,
            type: "cid"
          })
          .then(res => {
            this.$refs.apy_loding.style.display = "none";
            this.areanames = res.data;
          });
        this.biaoshi = "did";
      } else if (this.biaoshi == "did") {
        this.$refs.apy_loding.style.display = "block";
        axios
          .post("/review/area", {
            id: this.id1,
            type: "aid"
          })
          .then(res => {
            this.$refs.apy_loding.style.display = "none";
            this.areanames = res.data;
          });
        this.biaoshi = "cid";
      } else if (this.biaoshi == "cid") {
        this.$refs.apy_loding.style.display = "block";
        axios.post("/review/area").then(res => {
          this.$refs.apy_loding.style.display = "none";
          this.areanames = res.data;
        });
        this.biaoshi = "aid";
      }
    }
  }
};



