import Vue from 'vue'
import Router from 'vue-router'
//导航
import Index from '@/components/nav/index'
//添加
import Leave from '@/components/add/leave'
import Beaway from '@/components/add/beaway'
import Beout from '@/components/add/beout'
import Overtime from '@/components/add/overtime'
import Employ from '@/components/add/employ'
import Stamper from '@/components/add/stamper'
import Comapprova from '@/components/add/comapprova'
//编辑
import LeaveEdit from '@/components/edit/leave'
import BeawayEdit from '@/components/edit/beaway'
import BeoutEdit from '@/components/edit/beout'
import OvertimeEdit from '@/components/edit/overtime'
import EmployEdit from '@/components/edit/employ'
import StamperEdit from '@/components/edit/stamper'
import ComapprovaEdit from '@/components/edit/comapprova'
//列表
import Checkbyme from '@/components/list/checkbyme'
import Sendbyme from '@/components/list/sendbyme'
import Copybyme from '@/components/list/copybyme'
//详情
import Detail from '@/components/detail/index'
import Pdetail from '@/components/detail/pindex'//打印详情页
import Review from '@/components/detail/review' //同意、拒绝
import Transmit from '@/components/detail/transmit' //转交
import Comment from '@/components/detail/comment' //评论


Vue.use(Router)
export default new Router({
  routes: [
    {
        path: '/',//导航
        name: 'index',
        component: Index
    },
    {
        path: '/leave',//请假
        name: 'leave',
        component: Leave
    },
    {
        path: '/beaway',//出差
        name: 'beaway',
        component: Beaway
    },
    {
        path: '/beout',//外出
        name: 'beout',
        component: Beout
    },
    {
        path: '/overtime',//加班
        name: 'overtime',
        component: Overtime
    },
    {
        path: '/employ',//招聘
        name: 'employ',
        component: Employ
    },
    {
        path: '/stamper',//印章
        name: 'stamper',
        component: Stamper
    },
    {
        path: '/comapprova',//通用审批
        name: 'comapprova',
        component: Comapprova
    },
    {
        path: '/leave_edit/:id',//请假编辑
        name: 'leave_edit',
        component: LeaveEdit
    },
    {
        path: '/beaway_edit/:id',//出差编辑
        name: 'beaway_edit',
        component: BeawayEdit
    },
    {
        path: '/beout_edit/:id',//外出编辑
        name: 'beout_edit',
        component: BeoutEdit
    },
    {
        path: '/overtime_edit/:id',//加班编辑
        name: 'overtime_edit',
        component: OvertimeEdit
    },
    {
        path: '/employ_edit/:id',//招聘编辑
        name: 'employ_edit',
        component: EmployEdit
    },
    {
        path: '/stamper_edit/:id',//印章编辑
        name: 'stamper_edit',
        component: StamperEdit
    },
    {
        path: '/comapprova_edit/:id',//通用审批编辑
        name: 'comapprova_edit',
        component: ComapprovaEdit
    },
    {
        path: '/checkbyme',
        name: 'checkbyme',
        component: Checkbyme
    },
    {
        path: '/sendbyme',
        name: 'sendbyme',
        component: Sendbyme
    },
    {
        path: '/copybyme',
        name: 'copybyme',
        component: Copybyme
    },
    {
        path: '/detail/:id/:acid/:optype/:block',
        name: 'detail',
        component: Detail
    },
    {
        path: '/review/:rid/:status/:optype',
        name: 'review',
        component: Review
    },
    {
        path: '/transmit/:rid',
        name: 'transmit',
        component: Transmit
    },
    {
        path: '/comment/:rid',
        name: 'comment',
        component: Comment
    },
    {
        path: '/pdetail/:id',
        name: 'pdetail',
        component: Pdetail
    }
  ]
})



